"use client"

import * as React from 'react';
import Link from 'next/link';
import AppLayout from '@/components/app-layout';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from '@/components/ui/card';
import { DropdownMenu, DropdownMenuContent, DropdownMenuItem, DropdownMenuTrigger } from '@/components/ui/dropdown-menu';
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from '@/components/ui/table';
import { Badge } from '@/components/ui/badge';
import { MoreHorizontal, PlusCircle } from 'lucide-react';
import { mockQuestions, mockSubjects } from '@/lib/mock-data';
import type { Question } from '@/types';
import { Input } from '@/components/ui/input';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';

export default function QuestionsPage() {
    const [questions, setQuestions] = React.useState<Question[]>(mockQuestions);
    const [subjectFilter, setSubjectFilter] = React.useState<string>("all");
    const [difficultyFilter, setDifficultyFilter] = React.useState<string>("all");
    const [searchTerm, setSearchTerm] = React.useState<string>("");

    const filteredQuestions = questions
        .filter(q => subjectFilter === 'all' || q.subject === subjectFilter)
        .filter(q => difficultyFilter === 'all' || q.difficulty === difficultyFilter)
        .filter(q => q.question.toLowerCase().includes(searchTerm.toLowerCase()));

    return (
        <AppLayout>
            <div className="space-y-6">
                <div className="flex items-center justify-between">
                    <div>
                        <h1 className="text-3xl font-bold font-headline">My Questions</h1>
                        <p className="text-muted-foreground">Manage your personal question bank.</p>
                    </div>
                    <Button asChild>
                        <Link href="/questions/new">
                            <PlusCircle className="mr-2 h-4 w-4" />
                            Add New Question
                        </Link>
                    </Button>
                </div>
                
                <Card>
                    <CardHeader>
                        <CardTitle>All Questions</CardTitle>
                        <CardDescription>Browse, filter, and manage your saved questions.</CardDescription>
                         <div className="mt-4 flex flex-col md:flex-row gap-4">
                            <Input 
                                placeholder="Search questions..." 
                                className="max-w-sm"
                                value={searchTerm}
                                onChange={(e) => setSearchTerm(e.target.value)}
                            />
                            <Select value={subjectFilter} onValueChange={setSubjectFilter}>
                                <SelectTrigger className="w-full md:w-[180px]">
                                    <SelectValue placeholder="Filter by subject" />
                                </SelectTrigger>
                                <SelectContent>
                                    <SelectItem value="all">All Subjects</SelectItem>
                                    {mockSubjects.map(s => <SelectItem key={s.id} value={s.name}>{s.name}</SelectItem>)}
                                </SelectContent>
                            </Select>
                            <Select value={difficultyFilter} onValueChange={setDifficultyFilter}>
                                <SelectTrigger className="w-full md:w-[180px]">
                                    <SelectValue placeholder="Filter by difficulty" />
                                </SelectTrigger>
                                <SelectContent>
                                    <SelectItem value="all">All Difficulties</SelectItem>
                                    <SelectItem value="easy">Easy</SelectItem>
                                    <SelectItem value="medium">Medium</SelectItem>
                                    <SelectItem value="hard">Hard</SelectItem>
                                </SelectContent>
                            </Select>
                        </div>
                    </CardHeader>
                    <CardContent>
                        <div className="border rounded-md">
                            <Table>
                                <TableHeader>
                                    <TableRow>
                                        <TableHead>Question</TableHead>
                                        <TableHead>Subject</TableHead>
                                        <TableHead>Difficulty</TableHead>
                                        <TableHead>Language</TableHead>
                                        <TableHead><span className="sr-only">Actions</span></TableHead>
                                    </TableRow>
                                </TableHeader>
                                <TableBody>
                                    {filteredQuestions.length > 0 ? (
                                        filteredQuestions.map((q) => (
                                            <TableRow key={q.id}>
                                                <TableCell className="font-medium max-w-md truncate">{q.question}</TableCell>
                                                <TableCell>{q.subject}</TableCell>
                                                <TableCell><Badge variant="outline" className="capitalize">{q.difficulty}</Badge></TableCell>
                                                <TableCell><Badge variant="secondary">{q.language.toUpperCase()}</Badge></TableCell>
                                                <TableCell>
                                                    <DropdownMenu>
                                                        <DropdownMenuTrigger asChild>
                                                            <Button aria-haspopup="true" size="icon" variant="ghost">
                                                                <MoreHorizontal className="h-4 w-4" />
                                                                <span className="sr-only">Toggle menu</span>
                                                            </Button>
                                                        </DropdownMenuTrigger>
                                                        <DropdownMenuContent align="end">
                                                            <DropdownMenuItem asChild><Link href={`/questions/${q.id}/edit`}>Edit</Link></DropdownMenuItem>
                                                            <DropdownMenuItem className="text-destructive focus:text-destructive focus:bg-destructive/10">Delete</DropdownMenuItem>
                                                        </DropdownMenuContent>
                                                    </DropdownMenu>
                                                </TableCell>
                                            </TableRow>
                                        ))
                                    ) : (
                                        <TableRow>
                                            <TableCell colSpan={5} className="text-center h-24">No questions found.</TableCell>
                                        </TableRow>
                                    )}
                                </TableBody>
                            </Table>
                        </div>
                        {/* TODO: Add pagination */}
                    </CardContent>
                </Card>
            </div>
        </AppLayout>
    );
}
